import React from "react";
import classes from "./CompaniesList.module.scss";

const CompaniesList = props => {
	// Метод для вывода компаний на страницу
	const renderCompanies = (companies) => {
		const companyArr = [];


		for (let name in companies) {
			const key = `${props.afterTax} + ${Math.random()}`;

			const arrItem = (
				<div className={classes.row} key={key}>
					<span>{name}</span>
					<span>{companies[name].count}</span>
					<span>{companies[name].dividendPerShare}</span>
					<span>{Math.ceil(companies[name].count * companies[name].dividendPerShare * 100) / 100}</span>
					<span onClick={props.showModalHandlerDelete}>Del</span>
					<span>Red</span>
				</div>
			);

			companyArr.push(arrItem);
		}

		return companyArr
	};

	return (
		<div className={classes.CompaniesList}>
			<div className={classes.header}>
				<span>Имя компании</span>
				<span>Акции (кол-во)</span>
				<span>Дивиденд</span>
				<span>Прибыль (год)</span>
				<span>Действия</span>
			</div>
			{
				renderCompanies(props.companies)
			}
			{
				<div className={classes.footer}>
					<div className={classes.Total}>
						<span>
							Прибыль до вычета налогов
						</span>
						<span>
							{props.beforeTax}
						</span>
					</div>

					<div className={classes.Total}>
						<span>
						Прибыль после вычета налогов
						</span>

						<span className={classes.afterTax}>
							{props.afterTax}
						</span>
					</div>
				</div>
			}
		</div>
	)
}

export default CompaniesList;
