import React from "react";
import classes from './Main.module.scss'
import CompaniesList from "../../components/CompaniesList/CompaniesList";
import {Title} from "../../components/Title/Title";
import {Button} from "../../components/UI/Button/Button";
import {Modal} from "../../hoc/Modal/Modal";
import {Input} from "../../components/UI/Input/Input";
import {FadeBg} from "../../hoc/Modal/FadeBg";
import axios from '../../axios/axios-settings'
import {Preloader} from "../../components/Preloader/Preloader";

// TODO: создать общую переменную this.state.key, которая будет определять пользователя

export default class Main extends React.Component {
	state = {
		key: "aHmcqjPPBwXvW397FPL31TtkwfJ3",
		nameForDelete: '',
		showPreloader: true,
		modals: {
			isOpenModalAddCompany: false,
			isOpenModalDeleteCompany: false
		},
		companyName: {
			value: "",
			showError: false
		},
		count: {
			value: "",
			showError: false
		},
		dividendPerShare: {
			value: "",
			showError: false
		},
		company: {},
		companies: {},
		beforeTax: '',
		afterTax: ''
	};

	showModalHandler = (e, popup) => {
		const modals = {...this.state.modals};
		modals[popup] = true;

		if (popup === "isOpenModalDeleteCompany") {
			const nameForDelete = e.target.parentNode.querySelector('span').textContent;
			this.setState({modals, nameForDelete});
			return
		}

		this.setState({modals});
	};

	closeModalHandler = (e, popup) => {
		const modals = {...this.state.modals};
		modals[popup] = false;

		this.setState({modals})
	};

	// Удаление компаний
	deletePositionHandler = async (name) => {
		const response = await axios.delete(`/${this.state.key}/portfolio/companies/${name}.json`);

		if (response.status === 200) {
			this.setState({showPreloader: true});

			this.renderCompanies();
			this.closeModalHandler(null, "isOpenModalDeleteCompany")
		}
	};

	// Отправка запроса на сервер и добавление компании в общий список
	// TODO: в будущем сделать проверку, если компания уже существует, то нужно делать PUT, а не patch
	mainModalFormAddCompany = async (e) => {
		e.preventDefault();
		try {
			let company = Object.assign({}, this.state.company);

			const {companyName, count, dividendPerShare} = this.state;

			company = this.createCompany(companyName.value, count.value, dividendPerShare.value);

			const response = await axios.patch(`/${this.state.key}/portfolio/companies.json`, company);

			if (response.status === 200) {
				const modals = {...this.state.modals};
				modals.isOpenModalAddCompany = false;

				this.setState({
					company,
					modals
				});

				this.renderCompanies();
			}
		} catch (e) {
			console.error(e)
		}
	};

	// Создание компании для того, чтобы отправить её в будущем в базу
	createCompany = (name, count, dividendPerShare) => {
		return {
			[name]: {
				count,
				dividendPerShare
			}
		}
	};

	// 3 метода для записи компании (имя, акции, размер дива)
	getCompanyName = (e) => {
		const companyName = Object.assign({}, this.state.companyName);

		companyName.value = e.target.value;
		this.setState({companyName});
	};

	getShareCount = (e) => {
		const count = Object.assign({}, this.state.count);

		count.value = e.target.value;
		this.setState({count});
	};

	getDividendPerShare = (e) => {
		const dividendPerShare = Object.assign({}, this.state.dividendPerShare);

		dividendPerShare.value = e.target.value;
		this.setState({dividendPerShare});
	};
	// 3 метода для записи компании (имя, акции, размер дива)

	// Правильный подсчет дивидендов
	countDividends = (companies) => {
		let beforeTax = 0;
		let afterTax = 0;
		// Суммирую
		for (let name in companies) {
			beforeTax += (companies[name].count * companies[name].dividendPerShare);
		}
		// Округляю
		beforeTax = Math.round(beforeTax * 100) / 100;
		// Вычисляю читый доход без налогов и округляю
		afterTax = Math.round((beforeTax - (beforeTax * 0.13)) * 100) / 100;
		// озвращаю объект
		return {
			beforeTax, afterTax
		}
	};

	// Асинхронная операция на сервер, рендерит список компаний
	renderCompanies = async () => {
		try {
			// Импорт портфеля пользователя
			const response = await axios.get(`/${this.state.key}.json`);
			// Импорт компаний
			const {companies} = response.data.portfolio;
			const {beforeTax, afterTax} = this.countDividends(companies);

			if (response.status === 200) {
				this.setState({companies, beforeTax, afterTax, showPreloader: false});
			}

		} catch (e) {
			console.error(e)
		}
	};

	componentDidMount() {
		this.renderCompanies();
	}

	render() {
		return (
			<div className={classes.Main}>
				<Title title="Список компаний"/>

				{
					this.state.showPreloader
						? <Preloader/>
						: <CompaniesList
							companies={this.state.companies}
							beforeTax={this.state.beforeTax}
							afterTax={this.state.afterTax}
							showModalHandlerDelete={e => this.showModalHandler(e, "isOpenModalDeleteCompany")}
						/>
				}


				<div className={classes.addSection}>
					<Button
						classses={["green"]}
						name="Добавить компанию"
						onClick={e => this.showModalHandler(e, "isOpenModalAddCompany")}
					/>
				</div>

				{/* Модальное окно для добавления компании */}
				{
					this.state.modals.isOpenModalAddCompany
						?
						<React.Fragment>
							<Modal>
								<h3 className={classes.MainModalTitle}>Добавить компанию</h3>

								<form onSubmit={this.mainModalFormAddCompany}>
									<Input showError={this.state.companyName.showError}
										   errorMessage={"Поле не может быть пустым"}
										   onChange={this.getCompanyName} label={"Имя компании"}/>
									<Input showError={this.state.count.showError}
										   errorMessage={"Поле может содержать только цифры"}
										   onChange={this.getShareCount} label={"Количество акций"}/>
									<Input showError={this.state.dividendPerShare.showError}
										   errorMessage={"Поле может содержать только цифры"}
										   onChange={this.getDividendPerShare} label={"Размер дивиденда (в год)"}/>

									<Button
										classses={["blue"]}
										name="Добавить компанию"
										onClick={e => this.showModalHandler(e, "isOpenModalAddCompany")}
									/>
								</form>
							</Modal>
							<FadeBg onClick={e => this.closeModalHandler(e, "isOpenModalAddCompany")}/>
						</React.Fragment>
						: null
				}

				{/* Модальное окно для удаления компании */}
				{
					this.state.modals.isOpenModalDeleteCompany
						?
						<React.Fragment>
							<Modal>
								<h3 className={classes.deleteModal}>
									Вы точно хотите удалить компанию
									<br/>
									<br/>
									{this.state.nameForDelete}?
								</h3>

								<div className={classes.modalDeleteBtnBox}>

									<Button
										name="Да"
										classses={["red"]}
										onClick={e => this.deletePositionHandler(this.state.nameForDelete)}
									/>

									<Button
										name="Отменить"
										classses={["green"]}
										onClick={e => this.closeModalHandler(e, "isOpenModalDeleteCompany")}
									/>
								</div>
							</Modal>
							<FadeBg onClick={e => this.closeModalHandler(e, "isOpenModalDeleteCompany")}/>
						</React.Fragment>
						: null
				}

			</div>
		)
	}
}