import React from "react";
import classes from './Modal.module.scss'

export const Modal = props => {
	const cls = [classes.Modal];

	if (!props.classes) {

	} else {
		props.classes.forEach(item => cls.push(classes[item]));
	}

	return (
		<div className={cls.join(' ')}>
			{props.children}
		</div>
	)
};